@import "variables";

.sidebox-container {
  text-align: center;
  background-color: white;
  border-radius: 10px;
  max-width: 250px; }

.name-container {
  margin-bottom: .3rem;
  line-height: 1.3;
  h1 {
    padding-top: 1.5rem;
    font-family: "Helvetica Now Display",sans-serif;
    font-weight: 200;
    font-size: 2rem;
    strong {
      font-family: "Helvetica Now Display",sans-serif;
      font-weight: 700;
      font-size: 2rem; } } }
.social-container {
  margin-top: 1.2rem;
  display: inline-block;
  font-size: .875rem;
  margin-bottom: 1.2rem;
  a {
    color: #44566C;
    margin-left: .8rem; } }

.title-container {

  text-align: center;
  font-family: "Helvetica Now",sans-serif;
  font-weight: 200;
  font-size: .8rem;
  max-width: 130px;
  padding: 5px;
  margin: 1rem auto .2rem auto;
  border-radius: 10rem;
  color: $black;
  background-color: #F5F8F9; }
.details {

  border-radius: 0 0 10px 10px;
  background-color: #F5F8F9;
  padding: 1rem .3rem;
  line-height: 2.2rem;

  .info {
    text-align: left;
    padding-left: 2rem; }

  li {
    color: #44566C;
    font-family: "Helvetica Now", sans-serif;
    font-weight: 300;
    font-size: .9rem;
    word-wrap: break-word; } }
.download-button {
  button {
    padding: .6rem 1rem;
    border-radius: 1rem;
    background-color: #58DD7A;
    color: white;
    border: none;
    margin-top: 1.2rem;
    margin-bottom: 1.1rem; } }



